/*
====================================
== General globals and sections
====================================
*/
html {
  background-color: #ddebed; }

header {
  background-color: #0a0a0a;
  padding: 1.25rem 0 1.25rem 0; }

footer {
  background-color: #0a0a0a;
  color: #4d4d4d;
  height: auto;
  padding: 1.25rem 0 1.25rem 0; }

section {
  padding: 1.25rem 0 2.5rem 0; }

.section__light {
  background-color: #ddebed;
  color: #0a0a0a; }

.section__dark {
  background-color: #4d4d4d;
  color: #fcfcfc; }

/*
====================================
== Grids and stuff
====================================
*/
.contain {
  max-width: 1120px;
  margin-left: auto;
  margin-right: auto; }
  .contain:after {
    content: " ";
    display: block;
    clear: both; }

.flex-row {
  display: flex;
  align-items: center; }

/*
====================================
== Typography
====================================
*/
h1, h2, h3, h4, h5 {
  text-rendering: optimizeLegibility; }

h1 {
  font-size: 3.157rem;
  line-height: 5rem;
  font-weight: 700; }

h2 {
  font-size: 2.369rem;
  line-height: 2.5rem;
  font-weight: 300;
  letter-spacing: 2px; }

h3 {
  font-size: 1.778rem;
  line-height: 2.5rem;
  font-weight: 400;
  letter-spacing: 1px; }

h4 {
  font-size: 1.333rem;
  line-height: 1.25rem;
  font-weight: 700;
  letter-spacing: 0.5px;
  opacity: 0.87; }

p {
  font-size: 1rem;
  line-height: 1.25rem;
  font-weight: 400;
  opacity: 0.87; }

h5 {
  font-size: 0.75rem;
  line-height: 1.25rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px; }

/*
====================================
== Header and Navigation
====================================
*/
.logo {
  flex: 2 auto; }
  .logo img {
    height: 2.5rem; }

nav {
  flex: 1; }
  nav ul {
    list-style: none;
    display: flex;
    justify-content: space-between; }
  nav a {
    font-size: 1.333rem;
    line-height: 1.25rem;
    font-weight: 400;
    text-decoration: none;
    color: #fcfcfc; }

.nav-item {
  transition: border-color 1s ease;
  border-bottom: 3px solid;
  border-color: rgba(255, 255, 255, 0); }
  .nav-item:hover {
    border-bottom: 3px solid;
    border-color: white; }

/*
====================================
== Components
====================================
*/
.features--item {
  width: 23.72881%;
  float: left;
  margin-right: 1.69492%; }
  .features--item:last-child {
    float: right;
    margin-right: 0; }
